import React from 'react';
import ReactDOM from 'react-dom';
import Draggable from 'react-draggable';
import { Resizable, ResizableBox } from 'react-resizable';
import AnimateHeight from 'react-animate-height';
class UDResizeWindow extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      hidden: false,
      x: this.props.x,
      y: this.props.y,
      width: this.props.width,
      height: this.props.height
    }
  }

  componentWillMount() {
    this.pubSubToken = PubSub.subscribe(this.props.id, this.onIncomingEvent.bind(this));
    this.setState({
      x: this.props.x,
      y: this.props.y,
      height: this.props.height,
      width: this.props.width
    })
  }

  componentWillUnmount() {
    PubSub.unsubscribe(this.pubSubToken);
  }

  onIncomingEvent(eventName, event) {
    if (event.type === "requestState") {
      var data = {
        attributes: {
          hidden: this.state.hidden,
        }
      }
      UniversalDashboard.post(`/api/internal/component/element/sessionState/${event.requestId}`, data);
    }
    else if (event.type === "setState") {
      this.setState(event.state.attributes);
    }
    else if (event.type === "removeElement") {
      this.setState({
        hidden: true,
      });
    }
  }
  onResize = (event, { element, size, handle }) => {
    this.setState({ width: size.width, height: size.height });
  };
  toggle = () => {
    const { height } = this.state;

    this.setState({
      height: height === 0 ? 500 : 0,
    });
  };
  boggle = () => {
    this.setState({
      hidden: true,
    });
  };
  render() {
    if (this.state.hidden) {
      return null;
    }
    else {
      console.log(this.props.content);
      var content = this.props.content;
      if (!Array.isArray(content)) {
        content = [content]
      }
      content = content.map(x => {
        return UniversalDashboard.renderComponent(x);
      });
      const { height } = this.state
      const { width } = this.state
      const mystyle = {
        position: "absolute",
        'padding-left': "10px",
        'padding-right': "10px",
        'border-radius': "12px 12px 12px 12px",
        '-webkit-box-shadow': "7px 8px 10px 1px rgba(0,0,0,0.61)",
        'box-shadow': "7px 8px 10px 1px rgba(0,0,0,0.61)",
        'width': this.state.width + "px",
        'height': this.state.height + "px",
      };
      const myButton = {
        'box-shadow': "0px 5px 14px -7px #276873",
        background: "linear-gradient(tobottom,#599bb35 %,#408c99100 %)",
        'background-color': "#0744ad",
        'border-radius': "8px",
        border: "2px solid #4e6096",
        display: "inline-block",
        cursor: "pointer",
        color: "#ffffff",
        'font-family': "Arial",
        'font-size': "20px",
        'font-weight': "bold",
        padding: "13px 32px",
        'text-decoration': "none",
        'text-shadow': "0px 1px 0px #455c96"
      };
      const myClose = {
        'box-shadow': "0px 5px 14px -7px #276873",
        background: "linear-gradient(tobottom,#599bb35 %,#408c99100 %)",
        'background-color': "#D62839",
        'border-radius': "8px",
        border: "2px solid #C82433",
        display: "inline-block",
        cursor: "pointer",
        color: "#ffffff",
        'font-family': "Arial",
        'font-size': "20px",
        'font-weight': "bold",
        padding: "13px 32px",
        'text-decoration': "none",
        'text-shadow': "0px 1px 0px #455c96"
      };
      return (
        <Draggable
          axis="both"
          handle=".box"
          defaultPosition={{ x: this.props.x, y: this.props.y }}
          position={null}
          grid={[5, 5]}
          scale={1}
          bounds={document.body}
          offsetParent={document.body}
          onStart={this.handleStart}
          onDrag={this.handleDrag}
          onStop={this.handleStop}>
          <div>
            <span>
              <button style={myButton} onClick={this.toggle}>
                {height === 0 ? this.props.buttonMax : this.props.buttonMin}
              </button>
              <button style={myClose} onClick={this.boggle}>
                {this.props.buttonExit}
              </button>
            </span>
            <AnimateHeight
              duration={500}
              height={height} // see props documentation below
            >
              <ResizableBox className="resizebox" width={width} height={height} draggableOpts={{ grid: [25, 25] }} onResize={this.onResize} resizeHandles={this.props.resizeHandles} axis="both">
                <div className="box" style={mystyle}>
                  {content}
                </div>
              </ResizableBox>
            </AnimateHeight>
          </div>
        </Draggable>
      );
    }
  }
}

export default UDResizeWindow
